import BulletSprite from './sprite/BulletSprite';
import { BackgroundColor, BulletRadiusRange, BulletCount, CountFont, PlayerHeight } from './config';
import PlayerSprite from './sprite/PlayreSprite';

export default class Game {
  private ctx!: CanvasRenderingContext2D;
  private width: number;
  private height: number;
  /** æ¸¸æˆæ˜¯å¦å·²å¼€å§‹ */
  private isStart: boolean = false;
  /** å­å¼¹åˆ—è¡¨ */
  private bullets: BulletSprite[] = [];
  /** ç©å®¶ */
  private player: PlayerSprite;
  /** æ¸¸æˆè¿›è¡Œçš„ç§’æ•° */
  public second: number = 0;
  /** æ˜¯å¦ç§»åŠ¨ç«¯ */
  public isMobile: boolean;
  /** æ¸¸æˆç»“æŸæ—¶å›è°ƒ */
  onGameOver?: (count: number) => void;

  constructor(
    private canvas: HTMLCanvasElement,
    options: {
      width: number;
      height: number;
      isMobile?: boolean;
    }
  ) {
    this.isMobile = options.isMobile;
    const dpr = window.devicePixelRatio || 1;
    this.canvas.width = this.width = options.width * dpr;
    this.canvas.height = this.height = options.height * dpr;
    this.canvas.style.width = `${options.width}px`;
    this.canvas.style.height = `${options.height}px`;
    this.ctx = this.canvas.getContext('2d');
  }

  /** æ¸¸æˆåˆ†æ•° */
  get count() {
    return ~~(this.second / 1000);
  }

  /** å¼€å§‹æ¸¸æˆ */
  start() {
    if (this.isStart) return;

    // åˆå§‹åŒ–ç©å®¶
    this.player = new PlayerSprite(
      this.ctx,
      this.width / 2,
      this.height / 2,
      this.isMobile ? 'touch' : 'keyboard',
    );

    this.player.startListener();

		const step = () => {
      if (!this.isStart) {
        this.render();
        // GameOver
        this?.onGameOver(this.count);
        return;
      }
      // è®°å½•å½“å‰æ¸¸æˆæ—¶é—´
      this.second += 16.67;
			this.render();
      this.update();
			requestAnimationFrame(step);
		};
    this.isStart = true;
		requestAnimationFrame(step);
  }

  /** ç»“æŸæ¸¸æˆ */
  stop() {
    this.isStart = false;
    this.player?.stopListener();
    this.bullets.length = 0;
    this.second = 0;
  }

  /** ç”Ÿæˆä¸€ä¸ªå­å¼¹ */
  addNewBullet() {
    // å…ˆå‡è®¾ç›®æ ‡åœ¨å±å¹•æ­£ä¸­é—´
    const target = { x: this.width / 2, y: this.height / 2 };

    // ç”Ÿæˆä¸€ä¸ªéšæœºçš„å­å¼¹åŠå¾„
    const r = (Math.random() * (BulletRadiusRange[1] - BulletRadiusRange[0])) + BulletRadiusRange[0];

    // ç”Ÿæˆä¸€ä¸ªéšæœºä½ç½®ï¼ˆéšæœºä»å››ä¸ªæ–¹å‘è¿›å…¥ï¼‰
    const { x, y } = [
      { x: Math.random() * this.width, y: -r * 2 },                // ä¸Šé¢
      { x: Math.random() * this.width, y: this.height + (r * 2) }, // ä¸‹é¢
      { x: -r * 2, y: Math.random() * this.height },               // å·¦è¾¹
      { x: this.width + (r * 2), y: Math.random() * this.height }, // å³è¾¹
    ][~~(Math.random() * 4)]

    const bullet = new BulletSprite(
      this.ctx,
      x,
      y,
      r,
      target,
    )

    this.bullets.push(bullet);
  }

  /** ç»˜åˆ¶æ¸¸æˆ */
  render() {
    // æ¯æ¬¡ç»˜åˆ¶å‰éƒ½éœ€è¦å…ˆæ¸…ç©ºç”»å¸ƒ
    this.ctx.clearRect(0, 0, this.width, this.height);
    this.renderBackground();
    this.renderCount();
    this.player.render();
    this.bullets.forEach(bullet => bullet.render());
  }

  /** æ›´æ–°æ¸¸æˆ */
  update() {
    // æ›´æ–°ç©å®¶ä½ç½®
    this.player.update();

    // ä¸å…è®¸è¶…å‡ºæ¸¸æˆå±å¹•è¾¹ç¼˜
    const edge = PlayerHeight / 2;
    if (this.player.x < edge) this.player.x = edge;
    if (this.player.y < edge) this.player.y = edge;
    if (this.player.x > this.width - edge) this.player.x = this.width - edge;
    if (this.player.y > this.height - edge) this.player.y = this.height - edge;

    this.bullets = this.bullets.filter(bullet => {
      // æ›´æ–°å­å¼¹ä½ç½®
      bullet.update();

      // åˆ¤æ–­å­å¼¹æ˜¯å¦å°„ä¸­ç©å®¶
      if (this.player.isCrash(bullet)) {
        this.isStart = false;
      }

      // å¦‚æœé£å‡ºå±å¹•åˆ™å°†å­å¼¹é”€æ¯
      return !(
        bullet.x < -bullet.r * 2 ||               // é£å‡ºå·¦è¾¹å±å¹•
        bullet.x > this.width + (bullet.r * 2) || // é£å‡ºå³è¾¹å±å¹•
        bullet.y < -bullet.r * 2 ||               // é£å‡ºä¸Šè¾¹å±å¹•
        bullet.y > this.height + (bullet.r * 2)   // é£å‡ºä¸‹è¾¹å±å¹•
      );
    });

    // å¦‚æœå±å¹•ä¸­çš„å­å¼¹æ•°é‡ä½äºè®¾ç½®çš„æ•°é‡ï¼Œåˆ™è¡¥å…¨æ•°é‡
    for (let i = this.bullets.length; i < BulletCount; i++) this.addNewBullet();
  }

  /** ç»˜åˆ¶æ¸¸æˆèƒŒæ™¯ */
  renderBackground() {
    this.ctx.fillStyle = BackgroundColor;
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
  }

  /** ç»˜åˆ¶åˆ†æ•° */
  renderCount() {
    this.ctx.font = CountFont;
    this.ctx.textAlign = 'right';
    this.ctx.fillStyle = '#000';
    this.ctx.fillText(`${this.count}`, this.width - 20, 50);
  }
}
