import { PlayerColor, PlayerHeight, PlayerStep } from '../config';
import BulletSprite from './BulletSprite';
import Sprite from './Sprite';

/**
 * ç”¨äºŒè¿›åˆ¶è¡¨ç¤ºæ–¹å‘
 */
const enum Direction {
  Up = 1,
  Down = 2,
  Left = 4,
  Right = 8,
}

/** ç©å®¶ç²¾çµ */
export default class PlayerSprite extends Sprite {
  /** ç©å®¶é¢œè‰² */
  color: string = PlayerColor;
  /** ç©å®¶æ—‹è½¬çš„è§’åº¦ */
  angle: number = 0;
  /** ç©å®¶æ­£åœ¨å‰è¿›çš„æ–¹å‘ */
  direction: number = 0;
  /** æ‰‹æŒ‡æŒ‰ä½çš„åœ°æ–¹ */
  touchPos?: { x: number, y: number };

  constructor(
    ctx: CanvasRenderingContext2D,
    x: number,
    y: number,
    private controlType: 'keyboard' | 'touch' = 'keyboard'
  ) {
    super(ctx, x, y);
  }

  /** å¼€å§‹ç›‘å¬ç”¨æˆ·è¾“å…¥ */
  public startListener() {
    if (this.controlType === 'keyboard') {
      window.addEventListener('keydown', this.handleKeyDown);
      window.addEventListener('keyup', this.handleKeyUp);
    } else if (this.controlType === 'touch') {
      window.addEventListener('touchstart', this.handleTouchStart, { passive: false });
      window.addEventListener('touchmove', this.handleTouchStart, { passive: false });
      window.addEventListener('touchend', this.handleTouchEnd);
      window.addEventListener('touchcancel', this.handleTouchEnd);
    }
  }

  /** åœæ­¢ç›‘å¬ç”¨æˆ·è¾“å…¥ */
  public stopListener() {
    if (this.controlType === 'keyboard') {
      window.removeEventListener('keydown', this.handleKeyDown);
      window.removeEventListener('keyup', this.handleKeyUp);
    } else if (this.controlType === 'touch') {
      window.removeEventListener('touchstart', this.handleTouchStart);
      window.removeEventListener('touchmove', this.handleTouchStart);
      window.removeEventListener('touchend', this.handleTouchEnd);
      window.removeEventListener('touchcancel', this.handleTouchEnd);
    }
  }

  /** å¤„ç†ç©å®¶æŒ‰ä¸‹é”®ç›˜ */
  private handleKeyDown = (e: KeyboardEvent) => {
    switch (e.key) {
      case 'w':
      case 'ArrowUp':
        this.direction |= Direction.Up;
        break;
      case 's':
      case 'ArrowDown':
        this.direction |= Direction.Down;
        break;
      case 'a':
      case 'ArrowLeft':
        this.direction |= Direction.Left;
        break;
      case 'd':
      case 'ArrowRight':
        this.direction |= Direction.Right;
        break;
    }
  }

  /** å¤„ç†ç©å®¶æ¾å¼€æŒ‰é”® */
  private handleKeyUp = (e: KeyboardEvent) => {
    switch (e.key) {
      case 'w':
      case 'ArrowUp':
        this.direction &= ~Direction.Up;
        break;
      case 's':
      case 'ArrowDown':
        this.direction &= ~Direction.Down;
        break;
      case 'a':
      case 'ArrowLeft':
        this.direction &= ~Direction.Left;
        break;
      case 'd':
      case 'ArrowRight':
        this.direction &= ~Direction.Right;
        break;
    }
  }

  /** å¤„ç†æ‰‹æŒ‡è§¦æ‘¸ä½ç½® */
  handleTouchStart = (e: TouchEvent) => {
    e.preventDefault();
    const event = e.targetTouches[0];
    const dpr = window.devicePixelRatio || 1;
    this.touchPos = {
      x: event.pageX * dpr,
      y: event.pageY * dpr,
    }
  }

  /** è§¦æ‘¸ç»“æŸ */
  handleTouchEnd = () => {
    this.touchPos = null;
  }

  /** ç»˜åˆ¶ç©å®¶ */
  public render() {
    this.ctx.fillStyle = this.color;

    // æ—‹è½¬è§’åº¦
    this.ctx.save();

    // ç»˜åˆ¶ç©å®¶
    this.getPlayerPath();
    this.ctx.fill();

    // å°†æ—‹è½¬å¤ä½
    this.ctx.restore();
  }

  /** å¾—åˆ°ç©å®¶å›¾å½¢çš„è·¯å¾„ */
  public getPlayerPath() {
    this.ctx.beginPath();
    const angles = this.getPlayerAngles();
    this.ctx.moveTo(angles[0].x, angles[0].y);
    this.ctx.lineTo(angles[1].x, angles[1].y);
    this.ctx.lineTo(angles[2].x, angles[2].y);
    this.ctx.closePath();
  }

  /** å¾—åˆ°ä¸‰è§’å½¢çš„ä¸‰ä¸ªè§’ */
  public getPlayerAngles(): { x: number, y: number }[] {
    const d = PlayerHeight / 2;
    return [
      { x: this.x, y: this.y - d },
      { x: this.x - Math.cos(Math.PI / 6) * d, y: this.y + Math.sin(Math.PI / 6) * d },
      { x: this.x + Math.cos(Math.PI / 6) * d, y: this.y + Math.sin(Math.PI / 6) * d },
    ]
    // æ ¹æ®è½¬è½´å…¬å¼è¿›è¡Œæ—‹è½¬
    .map(({ x, y }) => ({
      x: this.x + (x - this.x) * Math.cos(this.angle) - (y - this.y) * Math.sin(this.angle),
      y: this.y + (y - this.y) * Math.cos(this.angle) + (x - this.x) * Math.sin(this.angle),
    }));
  }

  /** æ›´æ–°ç©å®¶ä½ç½® */
  public update() {
    if (this.controlType === 'touch') {
      if (!this.touchPos) return;
      // è·å–æ‰‹æŒ‡æŒ‰ä½çš„ä¸ç©å®¶çš„è§’åº¦
      const angle = Math.atan2(this.touchPos.y - this.y, this.touchPos.x - this.x);

      // æ ¹æ®è§’åº¦å°†è·ç¦»åˆ†ä¸ºæ°´å¹³è·ç¦»å’Œå‚ç›´è·ç¦»
      this.x += PlayerStep * Math.cos(angle);
      this.y += PlayerStep * Math.sin(angle);

      // å› ä¸ºç©å®¶çš„è§’åº¦æ˜¯æœä¸Šçš„ï¼Œè€Œæ­£å¸¸åæ ‡è½´æ˜¯æœå³çš„ï¼Œä¼°æ¯æ¬¡æ—‹è½¬éƒ½åŠ ä¸Š 90Â°
      this.angle = angle + Math.PI / 2;
    } else {
      // æŒ‰æ–¹å‘å‰è¿›
      if (this.direction & Direction.Up) {
        this.y -= PlayerStep;
      }
      if (this.direction & Direction.Down) {
        this.y += PlayerStep;
      }
      if (this.direction & Direction.Left) {
        this.x -= PlayerStep;
      }
      if (this.direction & Direction.Right) {
        this.x += PlayerStep;
      }
      // æŒ‰æ–¹å‘æ›´æ–°æ—‹è½¬è§’åº¦
      switch (this.direction) {
        case Direction.Up:
          this.angle = 0;
          break;
        case Direction.Down:
          this.angle = Math.PI;
          break;
        case Direction.Left:
          this.angle = Math.PI / 2 * 3;
          break;
        case Direction.Right:
          this.angle = Math.PI / 2;
          break;
        case Direction.Left | Direction.Up:
          this.angle = -Math.PI / 4;
          break;
        case Direction.Left | Direction.Down:
          this.angle = -Math.PI / 4 * 3;
          break;
        case Direction.Right | Direction.Up:
          this.angle = Math.PI / 4;
          break;
        case Direction.Right | Direction.Down:
          this.angle = Math.PI / 4 * 3;
          break;
      }
    }
  }

  /** åˆ¤æ–­å­å¼¹æ˜¯å¦å°„ä¸­ç©å®¶ */
  public isCrash(bullet: BulletSprite): boolean {
    let isCrash = false;

    // åˆ¤æ–­å­å¼¹åœ†å¿ƒæ˜¯å¦å·²ç»åœ¨ä¸‰è§’å½¢ä¸­
    this.ctx.save();
    this.getPlayerPath();
    isCrash = this.ctx.isPointInPath(bullet.x, bullet.y);
    this.ctx.restore();

    // å¦‚æœä¸åœ¨ï¼Œåˆ™åˆ¤æ–­åœ†å¿ƒåˆ°ä¸‰æ¡è¾¹çš„è·ç¦»
    if (!isCrash) {
      const angles = this.getPlayerAngles();
      for (let i = 0; i < angles.length; i++) {
        // å…ˆå¾—åˆ°ä¸€æ¡è¾¹çš„ä¸¤ä¸ªè§’
        const A = angles[i];
        const B = i === angles.length - 1 ? angles[0] : angles[i + 1];

        // è¿æ¥ä¸¤ä¸ªè§’å¾—åˆ°ä¸‰è§’å½¢è¾¹çš„å‘é‡ v1
        let v1 = { x: B.x - A.x, y: B.y - A.y };
        // è¿æ¥åœ†å¿ƒå’Œç¬¬ä¸€ä¸ªè§’å¾—åˆ°çš„å‘é‡ v2
        let v2 = { x: bullet.x - A.x, y: bullet.y - A.y };

        // ä¸‰è§’å½¢è¾¹çš„é•¿åº¦
        const len = Math.sqrt(v1.x * v1.x + v1.y * v1.y);

        // å°† v1 å•ä½åŒ–
        v1 = { x: v1.x / len, y: v1.y / len };

        // v1Â·v2 çš„ç‚¹ç§¯å°±æ˜¯æŠ•å½±çš„é•¿åº¦
        const u = v1.x * v2.x + v1.y * v2.y;

        // è¾¹ä¸Šç¦»åœ†å¿ƒæœ€è¿‘çš„ç‚¹
        let lx, ly;

        // å¦‚æœæŠ•å½±é•¿åº¦ <= 0ï¼Œåˆ™ä»£è¡¨åœ†å¿ƒåœ¨ä¸‰è§’å½¢è¾¹çš„å·¦è¾¹ï¼Œé‚£è·ç¦»æœ€è¿‘çš„ç‚¹å°±æ˜¯è§’A
        if (u <= 0) {
          lx = A.x;
          ly = A.y;
        }
        // å¦‚æœæŠ•å½±é•¿åº¦ >= è¾¹é•¿ï¼Œåˆ™ä»£è¡¨åœ†å¿ƒåœ¨ä¸‰è§’å½¢è¾¹çš„å³è¾¹ï¼Œé‚£è·ç¦»æœ€è¿‘çš„ç‚¹å°±æ˜¯è§’B
        else if (u >= len) {
          lx = B.x;
          ly = B.y;
        }
        // å¦‚æœéƒ½ä¸æ˜¯åˆ™åœ¨è¾¹çš„æ­£ä¸Šæ–¹
        else {
          lx = A.x + u * v1.x;
          ly = A.y + u * v1.y;
        }

        // åˆ¤æ–­åœ†å¿ƒè·ç¦»è¾¹æœ€è¿‘çš„ç‚¹æ˜¯å¦å°äºåŠå¾„
        if (Math.sqrt((bullet.x - lx) ** 2 + (bullet.y - ly) ** 2) < bullet.r) {
          isCrash = true;
          break;
        }
      }
    }
    return isCrash;
  }
}