import { BulletColorList, BulletOffset, BulletRadiusRange, BulletSpeedRange } from '../config';
import Sprite from './Sprite';

/** å­å¼¹ç²¾çµ */
export default class BulletSprite extends Sprite {
  /** å­å¼¹é¢œè‰² */
  color: string = BulletColorList[~~(Math.random() * BulletColorList.length)];
  /** æ°´å¹³é€Ÿåº¦ */
  vx!: number;
  /** å‚ç›´é€Ÿåº¦ */
  vy!: number;

  constructor(
    ctx: CanvasRenderingContext2D,
    x: number,
    y: number,
    /** åŠå¾„ */
    public r: number,
    /** æ”»å‡»ç›®æ ‡ */
    target: { x: number, y: number },
  ) {
    super(ctx, x, y);
    this.initBullet(target);
  }

  /** åˆå§‹åŒ–å­å¼¹ */
  initBullet(target: { x: number, y: number }) {
    // æ ¹æ®åŠå¾„å¾—åˆ°æˆ‘ä»¬çš„é€Ÿåº¦
    const speed = BulletSpeedRange[1] - (
      ((this.r - BulletRadiusRange[0]) / (BulletRadiusRange[1] - BulletRadiusRange[0])) // å­å¼¹åŠå¾„åœ¨åŠå¾„èŒƒå›´é‡Œçš„æ¯”ä¾‹
      * (BulletSpeedRange[1] - BulletSpeedRange[0]) // ä¹˜ä»¥é€Ÿåº¦çš„èŒƒå›´
    ); // å†åŠ ä¸Šæœ€ä½çš„é€Ÿåº¦

    // å­å¼¹åå·®åçš„ç»“æœä½ç½®
    const targetX = target.x + ((Math.random() * BulletOffset * 2) - BulletOffset);
    const targetY = target.y + ((Math.random() * BulletOffset * 2) - BulletOffset);

    // çŸ¥é“ä¸¤ç‚¹å°±èƒ½ç¡®å®šè§’åº¦äº†
    const angle = Math.atan2(targetY - this.y, targetX - this.x);

    // ä¸‰è§’å‡½æ•°å¾—åˆ°å­å¼¹çš„æ°´å¹³é€Ÿåº¦å’Œå‚ç›´é€Ÿåº¦äº†
    this.vx = Math.cos(angle) * speed;
    this.vy = Math.sin(angle) * speed;
  }

  /** ç»˜åˆ¶å­å¼¹ */
  public render() {
    this.ctx.fillStyle = this.color;
    this.ctx.beginPath();
    this.ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2, true);
    this.ctx.closePath();
    this.ctx.fill();
  }

  /** æ›´æ–°å­å¼¹ä½ç½® */
  public update() {
    this.x += this.vx;
    this.y += this.vy;
  }
}